<template>
  <div class="main flex">
    <section class="main__box" v-if="!propData">
      <button class="btn" @click="isNew()">New Character</button>
      <div
        class="main__list"
        v-for="(c, key) in char"
        :key="key"
        @click="propData = c"
      >{{c.nickname}}</div>
    </section>
    <section v-else class='main__box'>
      <profile :data="propData" />
    </section>
  </div>
</template>

<script>
import profile from "@/components/RegisterCharacter.vue";
import axios from "axios";

export default {
  components: {
    profile: profile
  },
  data() {
    return {
      char: null,
      propData: null
    };
  },
  methods:{
    isNew(){
      this.propData = {skills:[]}
      this.$store.commit("setCreateOrNot", true)
    }
  },
  created() {
    axios.get("/admin/persona").then(res => {
      this.char = res.data;
    });
  }
};
</script>