<template>
  <div>
    <div>
      Type:
      <select v-model="effect.type">
        <option v-for="(val, x) in type" :key="x" :value="x">{{ val }}</option>
      </select>
      Tick:
      <input @change="stringToInt('tick', effect.tick)" type="number" v-model="effect.tick" />
      Duration:
      <input @change="stringToInt('duration', effect.duration)" type="number" v-model="effect.duration" />
      Value:
      <input @change="stringToInt('value', effect.value)" type="number" v-model="effect.value" />
    </div>

    <div>
      Attribute:
      <select @change="stringToInt('attr', effect.attr)" v-model="effect.attr">
        <option v-for="(val, x) in attr" :key="x" :value="x">{{ val }}</option>
      </select>
      Trigger:
      <select @change="stringToInt('trigger', effect.trigger)" v-model="effect.trigger">
        <option v-for="(val, x) in trigger" :key="x" :value="x">{{ val }}</option>
      </select>
      Condition:
      <select @change="stringToInt('condition', effect.condition)" v-model="effect.condition">
        <option v-for="(val, x) in condition" :key="x" :value="x">{{ val }}</option>
      </select>
    </div>

    Auto target: <select @change="stringToInt('auto_target', effect.auto_target)" v-model="effect.auto_target">
      <option v-for="(val, x) in auto_target" :key="x" :value="x">{{ val }}</option>
    </select>
  </div>
</template>

<script>
export default {
  props: ["effect"],
  data() {
    return {
      e: this.effect,
      type: ["Reduce", "Increase", "Invulnerable"],
      condition: ["None", "checkAlly", "checkEnemy", "checkHP"],
      attr: ["None", "HP"],
      trigger: [
        "None",
        "onHPdrop",
        "onStrength",
        "onAgility",
        "onWisdom",
        "onSpirit",
        "onOffense",
        "onDefense"
      ],
      auto_target: [
        "None",
        "Self",
        "Ally",
        "All Allies",
        "Enemy",
        "All Enemies",
        "Random",
        "Ramdom Ally",
        "Random Enemy"
      ]
    };
  },
  methods: {
    stringToInt(hash, val) {
      this.effect[hash] = Number(val);
    }
  }
};
</script>