<template>
  <section class="main--2thirds">
    
    <h1>Skill Editor</h1>
    <div>
      <img v-if="imgUrl" :src="imgUrl" />
      <input type="file" @change="onChange" />
    </div>
    
    <input type="text" v-model="skill.skillName" placeholder="skill name" />
    
    <textarea v-model="skill.description" cols="70" rows="10" placeholder="skill description"></textarea>
    
    <input
      @change="stringToInt('cooldown', skill.cooldown)"
      type="number"
      v-model="skill.cooldown"
      placeholder="cooldown"
    />
    <div>
      <select @change="stringToInt('selection', skill.selection)" v-model="skill.selection">
        <option v-for="(val, key) in selection" :key="key" :value="key">{{val}}</option>
      </select>
    </div>

    <div>
      <div v-for="(c, key) in costs" :key="key">
        {{ c }}
        <input
          @change="stringToInt('costs', skill.costs[key], key)"
          type="number"
          v-model="skill.costs[key]"
        />
      </div>
    </div>

    <section>
      <h5>Effects</h5>
      <ul>
        <li>
          <button @click="createNewEffect()">Create new effect</button>
        </li>
        <li v-for="(e,key) in skill.effects" :key="key" @click="propData = e">{{ "Effect "+ key }}</li>
      </ul>
      <effects v-if="propData" :effect="propData" />
    </section>
  </section>
</template>

<script>
import effects from "@/components/editEffects.vue";
export default {
  props: ["data"],
  components: {
    effects: effects
  },
  data() {
    return {
      propData: null,
      skill: this.data.skill,
      selection: [
        "Enemy",
        "Ally",
        "Self",
        "All Enemies",
        "All Allies",
        "All",
        "Any"
      ],
      costs: ["Strength", "Wisdom", "Agility", "Spirit", "Random"],
      imgUrl: null
    };
  },
  methods: {
    stringToInt(hash, val, key) {
      if (key !== undefined) {
        this.skill[hash][key] = Number(val);
      } else {
        this.skill[hash] = Number(val);
      }
    },
    
    onChange(e) {
      const file = e.target.files[0];
      this.$store.commit("setSkillPic", { file: file, index: this.data.index });
      this.imgUrl = URL.createObjectURL(file);
    },

    createNewEffect() {
      console.log("you called?");
      if (!Array.isArray(this.skill.effects)) {
        this.skill.effects = [];
      }
      this.skill.effects.push({});
      console.log(this.skill.effects);
    }
  },
  watch: {
    data(n) {
      console.log(n)
      this.skill = n.skill;
      this.imgUrl = null;
    }
  }
};
</script>

<style lang="scss" scoped>
  .flex{
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: center;
  }

  select, input, textarea {
    margin: 5px;
  }

</style>