<template>
  <section class="flex flex--row">
    <div>
      <div class="main__list" @click="updateTab('profile', char)">Edit profile</div>
      <div
        class="main__list"
        v-for="(s, x) in char.skills"
        :key="x"
        @click="updateTab('skill', {skill: s, index: x})"
      >{{ "Skill " + (x+1) }}</div>
      <div class="btn" @click="createNewSkill">Create new skill</div>
      <div class="btn" @click="upload()">Upload</div>
    </div>
    <component class="main--scroll" v-if="propData" :is="comp" :data="propData"></component>
  </section>
</template>

<script>
import skill from "@/components/editSkill.vue";
import profile from "@/components/editProfile.vue";
import axios from "axios";

export default {
  components: {
    skill: skill,
    profile: profile
  },
  props: ["data"],
  data() {
    return {
      char: this.data,
      comp: null,
      propData: null
    };
  },
  methods: {
    updateTab(tab, data) {
      this.comp = tab;
      this.propData = data;
    },
    createNewSkill() {
      if (!Array.isArray(this.char.skills)) {
        this.char.skills = [];
      }
      this.char.skills.push({ costs: [0, 0, 0, 0, 0], effects: [] });
    },
    upload() {
      const pictures = this.$store.getters["allData"];
      const f = new FormData();
      const json = JSON.stringify(this.char);
      for (const x in pictures.skillpics) {
        console.log(x)
        f.append("skillpic_" + String(x), pictures.skillpics[x]);
      }

      f.append("facepic", pictures.facepic);
      f.append("json", json);

      if (this.$store.getters["isNew"]) {
        axios
          .post("/admin/new/persona", json)
          .then((res) => {
            if (res.status === 200)
              this.$router.go()
          })
          .catch(err => console.log(err));
      } else {
        axios
          .post("/admin/update/persona", f)
          .then(res => {
            if (res.status === 200)
              this.$router.go()
          })
          .catch(err => console.log(err));
      }
    }
  }
};
</script>