<template>
  <div class="profile main--2thirds">
    <img v-if="imgUrl" :src="imgUrl" />
    <div><input type="file" @change="onChange" /></div>
    <div>Name: <input type="text" v-model="p.nickname" /></div>
    <div><div>Description:</div><textarea v-model="p.profile" cols="30" rows="10"></textarea></div>
  </div>
</template>

<script>
export default {
  props: ["data"],
  data() {
    return {
      p: this.data,
      imgUrl: null
    };
  },
  methods: {
    onChange(e) {
      const file = e.target.files[0];
      this.$store.commit("setFacepic", file);
      this.imgUrl = URL.createObjectURL(file);
    }
  }
};
</script>

<style lang="scss" scoped>
  input {
    margin: 5px;
    color: whitesmoke;
    background-color: inherit;
    border-bottom: 1px dashed whitesmoke;
    padding: 1px;
  }
</style>